.main{
  padding: 10.25em 0 10em;
  background: radial-gradient(45.02% 45.02% at -14.74% 56.75%, #FFB422 4.07%, rgba(255, 180, 34, 0) 100%), radial-gradient(67.58% 67.58% at -13.86% 53.23%, #FFB422 1.49%, rgba(255, 180, 34, 0) 51.61%) , radial-gradient(42.19% 42.19% at -3.33% 41.61%, #FFB422 0%, rgba(255, 180, 34, 0) 100%) , radial-gradient(62.28% 49.4% at 28.14% 110.65%, #F457EC 9.89%, rgba(244, 87, 236, 0) 100%) , radial-gradient(38.93% 30.88% at 0.94% 24.13%, #FFB422 0%, rgba(255, 180, 34, 0) 100%) , radial-gradient(58.88% 46.71% at -6.51% 86.63%, #FF3838 36.46%, rgba(255, 56, 56, 0) 100%) , radial-gradient(50.1% 50.1% at 11.69% 14.46%, #F457EC 0%, rgba(244, 87, 236, 0) 100%), #1A9EFE;
  border-radius: 0 0 2.5em 2.5em;
  color: #fff;
  &__inner{
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  &__logo{
    margin-bottom: 1.25em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__btn{
    margin-top: 2.5em;
    width: 15.875em;
    max-width: 100%;
  }
}

.z-3{
  position: relative;
  z-index: 3;
}

.who,
.team,
.cool{
  .display1{
    margin-top: .142em;
    margin-left: -.06em;
  }
  &__text{
    max-width: 846px;
    margin: .625em 0 0 auto;
    position: relative;
    z-index: 2;
  }
}

.who{
  background-color: $black;
  padding: 12.5em 0 10em;
  margin-top: -2.5em;
  color: #FFFFFF;
  overflow: hidden;
  &__inner{
    position: relative;
    padding-bottom: 19em;
  }
  &__lips{
    position: absolute;
    top: 41.25em;
    width: 13.9375em;
    left: 29.25em;
    transform: rotate(-14deg);
  }
}

.round{
  width: 11em;
  height: 11em;
  border-radius: 50%;
  position: absolute;
  filter: blur(9.75em);
  background: #1A9EFE;
  pointer-events: none;
}

.round1{
  top: -1.375em;
  right: 5em;
}

.round2{
  top: 18.125em;
  left: .875em;
  background: $primary;
}

.round3{
  top: 30.25em;
  left: 9.25em;
}

.round4{
  right: 4.25em;
  top: 29.625em;
  opacity: 0.67;
  width: 18.8125em;
  height: 18.8125em;
}

.who-img{
  position: absolute;
  display: block;
}

.who-img1{
  width: 7.75em;
  top: 19em;
  left: 1.625em;
  transform: rotate(13deg);
  border-radius: 1.875em;
}

.who-img2{
  width: 15.125em;
  top: 33.25em;
  left: 1.6875em;
  transform: rotate(-9deg);
  border-radius: 3.125em;
}

.who-img3{
  top: 10.8125em;
  right: 4.5em;
  width: 9.9375em;
  border-radius: 2.5em;
  transform: rotate(10deg);
}

.who-img4{
  width: 23.25em;
  border-radius: 3.125em;
  top: 31.625em;
  right: -1.125em;
  transform: rotate(8deg);
}

.nums{
  margin-top: 10.625em;
  display: flex;
}

.num{
  flex-shrink: 0;
  position: relative;
  &:not(:last-child){
    margin-right: 15em;
  }
  &__img{
    height: 22.5em;
    width: auto;
    margin-bottom: 2.1875em;
    display: block;
  }
  &__ear{
    position: absolute;
    left: 19.25em;
    top: -6.25em;
    width: 12.25em;
    transform: matrix(-0.99, 0.14, 0.14, 0.99, 0, 0);
  }
  &__girl{
    position: absolute;
    top: 7em;
    left: 100%;
    margin-left: -5.625em;
    width: 21.4375em;
  }
}

.team{
  background-color: $primary;
  border-radius: 2.5em;
  margin-top: -2.5em;
  padding: 10em 0;
  position: relative;
  z-index: 2;
  &__inner{
    position: relative;
  }
  .display1{
    position: relative;
    z-index: 2;
  }
  &__text2{
    margin-top: 18.5em;
  }
  &__ear{
    position: absolute;
    left: -.1875em;
    top: 44.75em;
    width: 10.75em;
    display: block;
    transform: matrix(1, 0, 0, -1, 0, 0);
  }
  &__mask{
    position: absolute;
    width: 7.5em;
    right: 1.875em;
    top: 26.5em;
  }
  @media screen and (min-width: 993px){
    .swiper{
      &-wrapper{
        position: static;
        transform: unset !important;
      }
      &-slide{
        width: 12.5em !important;
        position: absolute;
        height: auto;
        &:nth-child(1){
          top: 0;
          right: 2.125em;
          transform: rotate(5.97deg);
        }
        &:nth-child(2){
          top: 18.625em;
          left: -2.8125em;
          z-index: 3;
          transform: rotate(-5deg);
        }
        &:nth-child(3){
          top: 31.25em;
          left: 8.5625em;
          transform: rotate(2.8deg);
        }
        &:nth-child(4){
          top: 33.25em;
          left: 23.375em;
          transform: rotate(-4deg);
        }
        &:nth-child(5){
          top: 30em;
          left: 37.8125em;
          z-index: 3;
          transform: rotate(-4deg);
        }
        &:nth-child(6){
          top: 36em;
          left: 50.25em;
          transform: rotate(4deg);
        }
      }
    }
  }
}

.team-card{
  padding: .5625em;
  background: #FFFFFF;
  border-radius: 1.25em;
  font-family: $font2;
  border: 1px solid $black;
  &__img{
    @include imgCover(89%);
    img{
      border-radius: .625em;
      display: block;
    }
  }
  &__content{
    padding: .75em 0;
    min-height: 6.25em;
    text-align: center;
  }
  &__title{
    font-weight: 600;
    font-size: 1.125em;
    line-height: 138.8%;
    letter-spacing: -0.02em;
  }
  &__prof{
    font-size: .875em;
    line-height: 114%;
    margin-top: .35em;
  }
}

.make{
  margin-top: -2.5em;
  background-color: $black;
  padding: 12.5em 0;
  color: #FFFFFF;
  &__inner{
    position: relative;
  }
  &__title{
    margin-top: .142em;
    margin-left: -.06em;
  }
  &__items{
    margin-top: -1.1875em;
    position: relative;
    z-index: 2;
  }
  &__title2{
    margin: -.42em 0 0 -.05em;
    width: calc(100% + .1em);
  }
  &__lips{
    top: 4.625em;
    right: -1.5625em;
    width: 14em;
    display: block;
    position: absolute;
  }
}

.make-card{
  padding: 1.25em;
  border-radius: 1.25em;
  background-color: $primary;
  display: grid;
  grid-template-columns: 20.1875em calc(100% - 20.1875em);
  min-height: 12em;
  color: $black;
  transform: rotate(2deg);
  &:nth-child(even){
    background-color: #fff;
    transform: rotate(-1deg);
  }
  &:nth-child(3n){
    transform: rotate(1deg);
  }
}

.round5{
  width: 28.5625em;
  height: 28.5625em;
  background-color: $primary;
  opacity: .67;
  left: -4em;
  bottom: 14.125em;
}

.round6{
  width: 18.8125em;
  height: 18.8125em;
  background-color: $primary;
  opacity: .67;
  right: -3.25em;
  top: 27.3125em;
}

.cool{
  padding: 10em 0;
  background-image: url(../img/cool.jpg);
  background-size: cover;
  background-position: center;
  border-radius: 2.5em;
  margin-top: -2.5em;
  overflow: hidden;
  &__top{
    color: #fff;
    position: relative;
  }
  &__butterfly{
    position: absolute;
    right: 20.9375em;
    top: 2.8125em;
    width: 7.0625em;
    transform: matrix(-0.97, -0.23, -0.23, 0.97, 0, 0);
  }
  &__tags{
    margin-top: 4.25em;
    display: flex;
    flex-wrap: wrap;
  }
  &__tag{
    font-family: $font2;
    padding: .5em 1.875em;
    min-height: 6.75em;
    border-radius: 1.25em;
    display: flex;
    align-items: center;
    background-color: #fff;
    margin: 0 .75em;
    //opacity: 0;
    &:nth-child(odd){
      background-color: $primary;
    }
    span{
      letter-spacing: -0.02em;
      font-size: 2.125em;
      line-height: 141%;
    }
    &:nth-child(1){
      transform: rotate(-1deg);
    }
    &:nth-child(2){
      margin-top: .3125em;
      transform: rotate(1deg);
    }
    &:nth-child(3){
      margin-top: -.4375em;
      transform: rotate(-3.01deg);
    }
    &:nth-child(4){
      margin-top: -.1875em;
      margin-left: auto;
      transform: rotate(-1deg);
    }
    &:nth-child(5){
      margin-top: .4375em;
      transform: rotate(-0.01deg);
    }
    &:nth-child(6){
      margin-top: .0625em;
      margin-left: 9.375em;
    }
  }
}

.work-here{
  margin-top: 10em;
  color: #fff;
  &__slider{
    margin-top: .8125em;
  }
  .swiper-scrollbar{
    height: 1.25em;
    margin-top: 1.875em;
    border-radius: .125em;
    background: rgba(255, 255, 255, 0.6);
    &-drag{
      border-radius: inherit;
      background: #fff;
    }
  }
}

.video{
  display: block;
  position: relative;
  &__play{
    @include centerAbsolute;
    width: 6.875em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__img{
    display: block;
    @include imgCover(50%);
    img{
      border-radius: 1.25em;
    }
  }
}

.main2{
  margin-top: -2.5em;
  position: relative;
  z-index: 2;
}


